import React from 'react';
import styles from './ContractorWindow.module.css';

const ContractorWindow = ({ onEditHandle, store }) => {
    const Contractor = store.state.currentDeal.contractor;
    if (!Contractor) {
        return null;
    }

    if (Contractor.id == 1) {
        return (
            <div>
                <div className={styles.dealDetailsContainer}>
                    <div className={styles.headerContainer}>
                        <div className={styles.dealTitle}>
                            : {Contractor.name || ' '}
                        </div>
                        <div className={styles.rightButtons}>
                            <button className={styles.editButton} onClick={onEditHandle} title="">
                                
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    return (
        <div className={styles.dealDetailsContainer}>
            <div className={styles.headerContainer}>
                <div className={styles.dealTitle}>
                    : {Contractor.name || ' '}
                </div>
                <div className={styles.rightButtons}>
                    <button className={styles.editButton} onClick={onEditHandle} title="">
                        
                    </button>
                </div>
            </div>

            <div className={styles.dealItem}>
                <strong>:</strong> {Contractor.INN}
            </div>
            <div className={styles.dealItem}>
                <strong>:</strong> {Contractor.phone}
            </div>
            <div className={styles.dealItem}>
                <strong>Email:</strong> {Contractor.email}
            </div>
            <div className={styles.dealItem}>
                <strong>:</strong> {Contractor.balance}
            </div>
            <div className={styles.dealItem}>
                <strong> ():</strong> {Contractor.balanceFree}
            </div>
            <div className={styles.dealItem}>
                <strong> :</strong> {Contractor.createdAt}
            </div>
            <div className={styles.dealItem}>
                <strong>:</strong> {Contractor.legalType_}
            </div>
            <div className={styles.dealLink}>
                  
            </div>
        </div>
    );
};

export default ContractorWindow;
